"use client";

import { useState } from "react";
import { motion } from "framer-motion";
import { Card } from "src/components/ui/card";
import { Input } from "src/components/ui/input";
import { Button } from "src/components/ui/button";
import { Label } from "src/components/ui/label";
import { Footer } from "src/components/ui/footer";
import { Background } from "src/components/ui/background";

const MotionDiv = motion.create("div");
const MotionCard = motion.create(Card);

export default function Home() {
  const [resumes, setResumes] = useState([]);
  const [jobCriteria, setJobCriteria] = useState({
    requiredSkills: "",
    minExperience: "",
    educationLevel: ""
  });
  const [searchTerm, setSearchTerm] = useState("");

  const handleFileUpload = async (event) => {
    const file = event.target.files?.[0];
    if (!file) return;

    if (!file.name.match(/\.(pdf|txt)$/)) {
      alert("Please upload a PDF or TXT file");
      return;
    }

    const formData = new FormData();
    formData.append("resume", file);
    formData.append("criteria", JSON.stringify(jobCriteria));

    try {
      const response = await fetch("/api/parseResume", {
        method: "POST",
        body: formData
      });

      if (!response.ok) throw new Error("Failed to process resume");

      const result = await response.json();
      setResumes([...resumes, result]);
    } catch (error) {
      console.error("Error:", error);
      alert("Failed to process resume");
    }
  };

  const filteredResumes = resumes.filter((resume) => {
    const searchLower = searchTerm.toLowerCase();
    return (
      resume.name.toLowerCase().includes(searchLower) ||
      resume.skills.some((skill) => skill.toLowerCase().includes(searchLower))
    );
  });

  return (
    <main className="min-h-screen text-white">
      <Background />
      <div className="max-w-6xl mx-auto p-8 space-y-8 relative">
        <MotionDiv 
          className="text-center"
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8 }}
        >
          <h1 className="text-6xl font-bold mb-4 bg-gradient-to-r from-purple-400 via-purple-500 to-indigo-500 text-transparent bg-clip-text font-outfit">
            AI Resume Screening System
          </h1>
          <p className="text-gray-400 mb-12 text-xl max-w-2xl mx-auto leading-relaxed">
            Upload resumes and find the best matches for your job requirements
          </p>
        </MotionDiv>

        <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
          <MotionDiv
            initial={{ opacity: 0, x: -50 }}
            animate={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.6, delay: 0.2 }}
          >
            <Card className="p-8">
              <h2 className="text-2xl font-semibold mb-8 bg-gradient-to-r from-white to-gray-300 bg-clip-text text-transparent">Upload Resume</h2>
              <div className="mb-8">
                <Input
                  type="file"
                  accept=".pdf,.txt"
                  onChange={handleFileUpload}
                  className="bg-black/50 border-gray-800 text-white file:mr-4 file:py-2 file:px-4 file:rounded-full file:border-0 file:bg-purple-500/10 file:text-purple-400 hover:file:bg-purple-500/20 file:transition-colors"
                />
              </div>

              <div className="space-y-8">
                <div>
                  <Label htmlFor="skills" className="text-gray-300">
                    Required Skills (comma-separated)
                  </Label>
                  <Input
                    id="skills"
                    value={jobCriteria.requiredSkills}
                    onChange={(e) =>
                      setJobCriteria({
                        ...jobCriteria,
                        requiredSkills: e.target.value
                      })
                    }
                    placeholder="e.g., Python, React, JavaScript"
                    className="mt-2 bg-black/50 border-gray-800 text-white"
                  />
                </div>

                <div>
                  <Label htmlFor="experience" className="text-gray-300">
                    Minimum Years of Experience
                  </Label>
                  <Input
                    id="experience"
                    type="number"
                    value={jobCriteria.minExperience}
                    onChange={(e) =>
                      setJobCriteria({
                        ...jobCriteria,
                        minExperience: e.target.value
                      })
                    }
                    placeholder="e.g., 3"
                    className="mt-2 bg-black/50 border-gray-800 text-white"
                  />
                </div>

                <div>
                  <Label htmlFor="education" className="text-gray-300">
                    Required Education Level
                  </Label>
                  <Input
                    id="education"
                    value={jobCriteria.educationLevel}
                    onChange={(e) =>
                      setJobCriteria({
                        ...jobCriteria,
                        educationLevel: e.target.value
                      })
                    }
                    placeholder="e.g., Bachelor's Degree"
                    className="mt-2 bg-black/50 border-gray-800 text-white"
                  />
                </div>
              </div>
            </Card>
          </MotionDiv>

          <MotionDiv
            initial={{ opacity: 0, x: 50 }}
            animate={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.6, delay: 0.4 }}
          >
            <Card className="p-8">
              <div className="flex justify-between items-center mb-6">
                <h2 className="text-2xl font-semibold bg-gradient-to-r from-white to-gray-300 bg-clip-text text-transparent">
                  Uploaded Resumes
                </h2>
                <Input
                  placeholder="Search resumes..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                className="max-w-xs bg-black/50 border-gray-800 text-white placeholder:text-gray-500/70"
                />
              </div>

              <div className="space-y-4 max-h-[500px] overflow-y-auto pr-2">
                {filteredResumes.length === 0 ? (
                <div className="text-center py-16">
                  <p className="text-gray-400 text-lg mb-3">No resumes uploaded yet</p>
                  <p className="text-gray-500">Upload a resume to get started and find the perfect match</p>
                  </div>
                ) : (
                  filteredResumes.map((resume, index) => (
                    <MotionDiv
                      key={resume.id}
                      initial={{ opacity: 0, y: 20 }}
                      animate={{ opacity: 1, y: 0 }}
                      transition={{ duration: 0.4, delay: index * 0.1 }}
                    >
                      <Card className="p-6">
                        <div className="flex justify-between items-start">
                          <div>
                            <h3 className="font-semibold text-lg text-white">{resume.name}</h3>
                            <p className="text-gray-400">{resume.email}</p>
                            <p className="text-gray-300 mt-2">
                              Education: {resume.education}
                            </p>
                            <p className="text-gray-300">
                              Experience: {resume.experience} years
                            </p>
                            <div className="flex flex-wrap gap-2 mt-3">
                              {resume.skills.map((skill) => (
                                <span
                                  key={skill}
                              className="bg-purple-500/10 text-purple-400 px-4 py-1.5 rounded-full text-sm font-medium border border-purple-500/20"
                                >
                                  {skill}
                                </span>
                              ))}
                            </div>
                          </div>
                          <div className="text-right">
                            <span className="inline-flex items-center px-4 py-1.5 rounded-full text-sm font-medium bg-green-500/10 text-green-400 border border-green-500/20">
                              Score: {resume.score}%
                            </span>
                          </div>
                        </div>
                      </Card>
                    </MotionDiv>
                  ))
                )}
              </div>
            </Card>
          </MotionDiv>
        </div>
      </div>
      <Footer />
    </main>
  );
}
